import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { IMapKeyValue, IOptionsPsActionHyperlink, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { IOptionsPsCashTransferExposed } from './ps-cash-transfer.component.interfaces';


/**
 * @author G.Radwan
 * @since 04/02/2020
 *
 * <p> PsCashTransferComponent is business component contains mapping for for container-lookup component to load local beneficiaries</p>
 */
@Component({
  selector: 'ps-cash-transfer',
  templateUrl: './ps-cash-transfer.component.html',
  styleUrls: ['./ps-cash-transfer.component.scss'],
})
export class PsCashTransferComponent extends PsTemplateListPage implements OnInit {
  @Output() reloadFct = new EventEmitter<string>();
  @Input() options: IOptionsPsCashTransferExposed = {};
  @Output() public onClickCard = new EventEmitter<any>();
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  private showSingleAccount = false;

  public addBeneficiaryOptions: IOptionsPsActionHyperlink = {
    route: 'cash-transfer',
    labelKey: CommonBussinessConstant.CASH_TRANSFER_DESC,
    iconOptions: {
      iconName: 'add-benef',
      labelOptions: {
        labelKey: 'add_cash_transfer_key'
      }
    },
    pageOptions: {
      operId: CommonBussinessConstant.CASH_BENEFICIARY_OPER_ID,
      title: CommonBussinessConstant.CASH_TRANSFER_DESC,
      iconName: 'beneficiaries',
    }
  };
  public updateList: any[];
  public bRefresh = false;

  constructor(
  ) {
    super();
  }

  ngOnInit() {
    super.init();
    this.populateCashTransferMap();
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }

    // this.containerLookUpOptions.balanceMapping = this.balanceMapping();
    if (this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }

  }

  getContainerLookUpOptions() {
    return this.prepareLookUp();
  }

  private prepareLookUp(): any {
    let lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.labelKey = this.options.labelKey;
    lookupDetails.fcName = this.options.fcName;
    lookupDetails.currencyFlag = 'currencyDesc';
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.itemList = this.options.itemList;
    lookupDetails.editActionUrl = PsCommonBusinessSettings.serviceUrl.personalizationNicknameEndPoint;
    lookupDetails.requestMap = this.requestMap;
    lookupDetails.editRequestMap = this.editRequestMap;
    lookupDetails.group = this.options.group;

    lookupDetails.actionDetailsOptions = [
      {
        id: 'delete',
        buttonIcon: 'delete',
        actionUrl: PsCommonBusinessSettings.serviceUrl.deleteBeneficiaryEndPoint,
        cssClass: 'fab-edit-button',
        labelOptions: {
          labelKey: 'delete_key'
        },
        label: 'delete_key',
        iconName: 'trash',
        actionType: 'delete',
        alertType: 'prompt',
        alertStyle: 'info',
        reloadList: true
      },
      {
        id: 'edit',
        label: 'edit_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'edit1',
            labelOptions: {
              labelKey: 'edit_key'
            }
          },
          route: 'cash-transfer',
          navigationOptions: {
            queryParams: { readOnlypage: false },
          },
          pageOptions: {
            operId: CommonBussinessConstant.CASH_BENEFICIARY_OPER_ID,
            iconName: 'edit1',
            title: 'edit_key'
          }
        }
      },
      {
        id: 'pay',
        label: 'pay_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'create',
            labelOptions: {
              labelKey: 'pay_key'
            }
          },
          route: 'payment',
          navigationOptions: {
            queryParams: { readOnlypage: false },
          },
          pageOptions: {
            operId: CommonBussinessConstant.CASH_TRANSFER_OPER_ID,
            iconName: 'create',
          },
          preCallFunction: {
            func() {
              return new Promise<any>((resolve, reject) => {
                resolve(this.executionClass.prepareTransaction(lookupDetails.actionDetailsOptions[2].actionHyperlink));
              });
            },
            executionClass: this,
            params: []
          },
        }
      }
    ];
    lookupDetails.statementOptions = {};
    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }
    return lookupDetails;
  }

  reloadList() {
    this.reloadFct.emit();
  }

  public updateCard(account: any) {
    if (account && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === account);
      this.options.item = this.options.itemList[0];
      this.showSingleAccount = !this.showSingleAccount;
      this.options.showItemPopUp = !this.options.showItemPopUp;
    }
    this.onClickCard.emit(this.options);
  }



  /**
   * populating the map for internal beneficiaries
   */
  private populateCashTransferMap() {
    let idTypeMap: IMapKeyValue = {};
    let idNumberMap: IMapKeyValue = {};
    let mobileNumberMap: IMapKeyValue = {};
    let purposeMap: IMapKeyValue = {};

    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};

    // load value on card expend

    idTypeMap = {
      key: 'idTypeDescriptionConcat',
      value: '------',
      isEdit: false
    };

    idNumberMap = {
      key: 'idNumber',
      value: '------',
      isEdit: false
    };

    mobileNumberMap = {
      key: 'cashBenefPhoneKey',
      value: '------',
      isEdit: false
    };

    purposeMap = {
      key: 'cashPurposeKey',
      value: '------',
      isEdit: false
    };

    headerTitleMap = {
      key: 'cashTitleKey',
      value: 'benefName',
      isEdit: false
    };

    headerSubtitleMap = {
      key: 'subTitle',
      value: 'cashBenefNameKey',
      isEdit: true
    };


    // key here will be used as label in look up details component
    this.labelValuesMap.set('id_type_key', idTypeMap);
    this.labelValuesMap.set('id_number_key', idNumberMap);
    this.labelValuesMap.set('mobile_number_key', mobileNumberMap);
    this.labelValuesMap.set('purpose_key', purposeMap);

    // will be used as header to template card in look up details component
    this.headerMap.set('title', headerTitleMap);
    this.headerMap.set('subTitle', headerSubtitleMap);
    this.requestMap.set('nickNameNumber', 'nickNameNumber');
    this.requestMap.set('oldNickName', 'benefName');
    this.editRequestMap.set('newNickName', 'benefName');
    this.editRequestMap.set('type', 'A');
  }

  prepareTransaction(actionCard: IOptionsPsActionHyperlink) {
    actionCard.navigationOptions.queryParams.ScreenVO = {
      toAccountType: 'cashTransfer',
      showBeneficiaryDetails: true,
      toBeneficiary: actionCard.navigationOptions.queryParams.idNumber,
      toBeneficiary_lookupKey: actionCard.navigationOptions.queryParams.idNumber,
      purposeTxtDesc: actionCard.navigationOptions.queryParams.purpose,
    };
  }

}

